import React from 'react'

function ProductDetail(props) {
    const {data} = props;
    const productImgs = data?.urlList;
    return (
        <>
            <div className="mt-2">
                <h2 className='font-bold uppercase text-lg my-1'>Chi tit sn phm</h2>
                <div className="">
                    {/*Product Image*/}
                    <div className="">
                        {productImgs?.map((img, index) => (
                            <img
                                key={index}
                                src={img}
                                alt={'product image - ' + index}
                                className="h-full w-full"
                            />
                        ))}
                    </div>
                    {/*EndProduct Image*/}

                    {/*Product Info*/}
                    <div className="mt-2">
                        <h2 className='font-bold uppercase text-lg my-1'>M t sn phm</h2>
                        <article className='text-pretty'>
                            <div style={{ wordBreak:"break-word", padding:"10px" }} dangerouslySetInnerHTML={{ __html: data?.description }}></div>
                        </article>
                    </div>
                </div>
            </div>
        </>
    )
}

export default ProductDetail
